.side-bar-cover {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.54);
  z-index: 999;
  top: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms; }



