.api-table {
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
  border: 1px solid #e9e9e9;
  width: 100%; }
  .api-table th {
    border: 1px solid #e9e9e9;
    padding: 8px 16px;
    text-align: left;
    background: #f7f7f7;
    white-space: nowrap;
    color: #5c6b77;
    font-weight: 600; }
  .api-table td {
    border: 1px solid #e9e9e9;
    padding: 8px 16px;
    text-align: left;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.65);
    line-height: 1.7; }
    .api-table td:first-child {
      background: #fcfcfc;
      font-weight: 500; }
    .api-table td:last-child {
      max-width: 200px; }



